/* My Work */

.my-work {
  background-color: var(--clr-dark);
  color: var(--clr-light);
  text-align: center;
}

.portfolio {
  display: grid;
  /* Change the first number to change # of columns */
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  /* max-width: 1500px; */
  margin: 0 auto;
}

/* .portfolio__name {
    align-items: center;
} */

.portfolio-selector {
  display: flex;
  justify-content: center;
  margin-bottom: 1em;
}

.portfolio-selector button {
  margin: 0 1em;
}

.portfolio-item {
  background-color: #e0c216;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Adjust shadow values as needed */
  transition: transform 0.3s ease-in-out;
  margin: 0.5em;
  color: var(--clr-dark);
}

.portfolio-item--header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.portfolio-item--header {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
}

.right-descriptor {
  background-color: #8b8989; /* Set the same background color for the text */
  color: #ffffff;
  padding: 10px;
  opacity: 0.8;
}

.portfolio__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 750ms cubic-bezier(0.5, 0, 0.5, 1), opacity 250ms linear;
}

.portfolio__item:focus {
  position: relative;
  z-index: 2;
}

.portfolio__img:hover,
.portfolio__item:focus .portfolio__img {
  transform: scale(1.2);
  opacity: 0.75;
}

/* Footer */

.footer {
  background: #111;
  color: var(--clr-accent);
  text-align: center;
  padding: 2.5em 0;
  font-size: var(--fs-h3);
}

.footer a {
  color: inherit;
  text-decoration: none;
}

.footer__link {
  font-weight: var(--fw-bold);
}

.footer__link:hover,
.social-list__link:hover {
  opacity: 0.7;
}

.footer__link:hover {
  text-decoration: underline;
}

.social-list {
  list-style: none; /*gets rid of the bullets */
  display: flex;
  justify-content: center;
  margin: 2em 0 0;
  padding: 0;
}

.social-list__item {
  margin: 0 0.5em;
}

.social-list__link {
  padding: 0.5em;
}

/* Individual portfolio item styles */
.portfolio-item-individual {
  padding: 0 2em 2em;
  max-width: 1000px;
  margin: 0 auto;
}

.portfolio-item-indivdual p {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}
